button {
  height: 70px;
  background: #EEA47FFF;
  outline: none;
  border: none;
  padding: 1rem 3rem;
  border-radius: 0.5rem;
  color: #00539CFF;
  font-family: 'bungee';
  font-size: 2rem;
  margin-top: 5rem;
  user-select: none;

  &:active {
    background: rgb(151, 83, 49);
    font-size: 2.2rem;
  }
}