p {
  font-size: 4rem;
  color: #00539CFF;
  margin: 0;
}
@keyframes changeNumber {
  @for $i from 1 through 100 {
    #{$i}% {
      content: "#{random(99)}";
    }
  }
}
.number-changer {
  font-size: 4rem;
  height: 64px;
  display: inline-block;
  color: #00539CFF;
}
.number-changer::after {
  content: '0';
  animation: changeNumber 20s linear infinite alternate;
}